import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { DiscussionForumService } from 'src/app/core/services/discussion-forum.service';
import { Topic } from 'src/app/Models/topic.model'
import { Section } from 'src/app/Models/section.model';
@Component({
  // selector: 'app-create-quote',
  templateUrl: "./create-topic.component.html",
  styleUrls: ["./create-topic.component.css"],
})
export class CreateTopicComponent implements OnInit {
  sucesserror: string="";
  objTopic = new Topic();
  objScetion = new Section();

  respone: any;
  constructor(
    public datepipe: DatePipe,
    private createQuoteService: CreateQuoteService, 
    private commonService: CommonService,
    private discussionfourmService: DiscussionForumService)
    { 
      this.objTopic.section =  this.objScetion;
    }

  ngOnInit() 
  {
    this.sucesserror="";
    this.getSectionList();
  }
  
  sectionList: any;
  getSectionList() 
  {
    this.discussionfourmService.getAllSections().subscribe((res) => {
      this.sectionList = res;
      console.log(res);
    });
  }

  onSectionChange(event)
  {
    this.objTopic.section.id = event.target.value;
    console.log(this.objTopic.id);
  }

  saveTopicDetails()
  {
    if(this.objTopic.section.id=="" || this.objTopic.section.id==null)
    {
      this.sucesserror="Please select Section for creating Topic";
      $("#AlertModal").show();
      return;
    }
    if(this.objTopic.title == "" || this.objTopic.title == null)
    {
      this.sucesserror="Please enter title";
      $("#AlertModal").show();
      return;
    }
    if(this.objTopic.content == "" || this.objTopic.content == null)
    {
      this.sucesserror="Please enter content";
      $("#AlertModal").show();
      return;
    }
    this.discussionfourmService.createTopicDetails(this.objTopic).subscribe((res) => {
    this.respone=res; 
    console.log(this.respone)
    this.objTopic.section.id="";
    this.objTopic.title="";
    this.objTopic.content="";
    this.sucesserror="Topic Created Successfully";
    $("#AlertModal").show();
    });
  }
}
